#_2YZJT {
  display: grid;
  align-items: center;
}

#_2YZJT > ._2fnJs {
  background: transparent;
  box-shadow: none;
}

#_2YZJT > ._2fnJs > span > h2 {
  font-size: 4rem;
}

#_2YZJT > ._2fnJs > p {
  font-size: 1.5rem;
}

#_2YZJT > ._2fnJs, 
#_2YZJT > ._2fnJs > span > h2, 
#_2YZJT > ._1bIDH > h1  {
  color: var(--font-color);
}

#_2YZJT > ._1bIDH {
  background: var(--bg-color-2);
  width: 80%;
  margin: 0 auto;
}

#_2YZJT > ._1bIDH > h1 {
  text-align: center;
}

#_2YZJT > ._1bIDH > span {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

#_2YZJT > ._1bIDH > span > span > svg {
  height: 100px;
  width: 100px;
  opacity: .8;
  transform: scale(.6);
  transform-origin: center;
  transition: all 300ms ease-in-out;
}

#_2YZJT > ._1bIDH > span > span > svg:hover {
  transform: scale(.8);
  opacity: 1;
}

/* to desktop */

@media (min-width: 768px) { 
  #_2YZJT > ._1bIDH { 
    width: 70%;
  }
}